function escapeRegex(string) {
  return string.replace(/[-\/\\^$*+?.()|[\]{}]/g, '\\$&')
}

export default function asciiToEmoji(text) {
  let result = text
  if (text && typeof text === 'string') {
    for (const asciiEmoji in EMOJIS_MAP) {
      const regexReplace = new RegExp(escapeRegex(asciiEmoji), 'g')
      const unicodeEmoji = EMOJIS_MAP[asciiEmoji]
      result = result.replace(regexReplace, unicodeEmoji)
    }
  }
  return result
}
